// Settings

@font-face {
    font-family: 'Kumbh Sans Regular';
    src: url('./assets/KumbhSans-Regular.ttf') format('truetype');
  }

  @font-face {
    font-family: 'Kumbh Sans Bold';
    src: url('./assets/KumbhSans-Bold.ttf') format('truetype');
  }

  @font-face {
    font-family: 'Amiri Regular';
    src: url('./assets/Amiri-Regular.ttf') format('truetype');
  }

  @font-face {
    font-family: 'Julius Sans One Regular';
    src: url('./assets/JuliusSansOne-Regular.ttf') format('truetype');
  }

  $font-sans-regular: 'Kumbh Sans Regular', sans-serif;
  $font-sans-bold: 'Kumbh Sans Bold', sans-serif;
  $font-serif-regular: 'Amiri Regular', serif;
  $font-title: 'Julius Sans One Regular', serif;

  @mixin main-font-scale {
    font-size: 16px;
    line-height: 28px;

    @media only screen and (min-width: 1024px) {
      font-size: 24px;
      line-height: 36px;
    }
  }

  // Colors

  $yellow: #f9f74b;
  $black: #011627;
  $blue: #0081a7;
  $turquoise: #00afb9;
  $salmon: #f07167;

  // Layout

  * {
    box-sizing: border-box;
  }

  body {
    background-color: $yellow;
    margin: 0;
    padding: 0;
    display: grid;
    // grid-template-rows: 200px 50px;
    // grid-template-rows: auto 1fr auto;
  }

  body > * {
      border: 2px dotted black;
  }
